
.ds-calendar-event-title {
  font-size: 18px;
  padding-right: 8px;
  padding-top: 4px;
}
.ds-color-option {
  width: 100%;
  color: white;
  padding: 4px;
}
.ds-button-tall {
  height: 48px;
}
.ds-event.ds-has-cancel .ds-event-area {
  margin-left: 60px;
}
.ds-event.ds-event-small.ds-has-cancel .ds-event-area {
  margin-left: 0px;
}
.ds-event.ds-event-small.ds-has-cancel .ds-event-header {
  margin-left: 60px;
  margin-bottom: 58px;
}
.ds-event.ds-event-small .ds-event-title {
  position: absolute;
  right: 8px;
  left: -60px;
  top: 60px;
}
.ds-event.ds-event-small .ds-event-body {
  clear: both;
}
.ds-event .ds-event-area {
  position: relative;
}
.ds-event .ds-event-actions {
  float: right;
}
.ds-event .ds-event-actions > * {
    display: inline-block;
}
.ds-event .ds-event-header {
  min-height: 60px;
}
.ds-event .ds-event-cancel {
  position: absolute;
  left: -60px;
}



